import React, { PureComponent } from "react";
import { GET_IMAGE_SIZE } from "../../../utils";
import { isSocialWall } from "../../../actions/api";


export default class HeaderTypeCustom extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      height: '5',
      width: '100',
      error: ''
    };
  }
  async componentWillMount() {
    const { ImageUrl } = this.props
    const { width, height } = await GET_IMAGE_SIZE(ImageUrl);
    this.setState({
      height: (height * 100) / width,
      width: (width * 100) / height
    })
  }
  render() {
    const ImageUrl = { backgroundImage: `url(${this.props.ImageUrl})`, paddingBottom: this.state.height + '%', width: '100%' }
    return (
      <div className="tb_wall_custom_header_wrap__">
        <div className={`tb_header_img_ ${isSocialWall ? `socialwall` : ``}`} style={ImageUrl}> </div>
      </div>
    );
  }
}
